"use client";

import React, { useState } from "react";
import { motion } from "framer-motion";
import { X, Plus, UserPlus } from "lucide-react";

/**
 * CreateTeamForm
 * Props:
 *  - open: boolean -> whether modal is visible
 *  - onCreate: (team) => void -> called with new team object
 *  - onClose: () => void -> called to close modal
 *
 * Notes:
 *  - When open === true this renders a fixed overlay with backdrop-blur
 *  - Minimal validation: team name required
 */
export default function CreateTeamForm({ open, onCreate, onClose }: any) {
  const initialState = {
    name: "",
    description: "",
    lead: "",
    featured: false,
    gradient: "from-blue-500 to-cyan-500",
    projects: 0,
    completion: 0,
    members: [""], // array of member names
  };

  const [form, setForm] = useState(initialState);
  const [errors, setErrors] = useState<any>({});

  if (!open) return null;

  const setField = (k: string, v: any) => setForm((s: any) => ({ ...s, [k]: v }));

  function memberInitials(name: string) {
    if (!name) return "";
    const parts = name.trim().split(/\s+/);
    if (parts.length === 1) return parts[0].slice(0, 2).toUpperCase();
    return (parts[0][0] + (parts[1]?.[0] ?? "")).toUpperCase();
  }

  function handleAddMember() {
    setForm((s: any) => ({ ...s, members: [...s.members, ""] }));
  }

  function handleRemoveMember(i: number) {
    setForm((s: any) => ({ ...s, members: s.members.filter((_: any, idx: number) => idx !== i) }));
  }

  function handleMemberChange(i: number, value: string) {
    setForm((s: any) => {
      const members = [...s.members];
      members[i] = value;
      return { ...s, members };
    });
  }

  function validate() {
    const e: any = {};
    if (!form.name.trim()) e.name = "Team name is required";
    if (form.completion < 0 || form.completion > 100) e.completion = "Must be 0â€“100";
    // add more validation as needed
    setErrors(e);
    return Object.keys(e).length === 0;
  }

  function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    if (!validate()) return;

    const newTeam = {
      id: Date.now(),
      name: form.name.trim(),
      description: form.description.trim(),
      lead: form.lead.trim() || undefined,
      featured: !!form.featured,
      gradient: form.gradient,
      projects: Number(form.projects) || 0,
      completion: Math.max(0, Math.min(100, Number(form.completion) || 0)),
      members: form.members
        .map((m: string) => m.trim())
        .filter(Boolean)
        .map((name: string) => ({ name, initials: memberInitials(name) })),
    };

    onCreate?.(newTeam);
    setForm(initialState);
    onClose?.();
  }

  const gradients = [
    "from-blue-500 to-cyan-500",
    "from-violet-500 to-purple-500",
    "from-orange-500 to-rose-500",
    "from-emerald-500 to-teal-500",
    "from-cyan-500 to-blue-500",
    "from-pink-500 to-rose-500",
  ];

  return (
    <div
      className="fixed inset-0 z-50 flex items-center justify-center"
      aria-modal="true"
      role="dialog"
    >
      {/* Overlay with blur */}
      <div
        className="absolute inset-0 bg-black/40 backdrop-blur-sm"
        onClick={() => onClose?.()}
      />

      <motion.form
        initial={{ opacity: 0, y: 12 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.25 }}
        onSubmit={handleSubmit}
        className="relative z-10 w-full max-w-2xl mx-4 bg-white rounded-2xl border border-slate-200 p-6 shadow-2xl"
        onClick={(e) => e.stopPropagation()}
      >
        <div className="flex items-start justify-between mb-4">
          <div>
            <h3 className="text-lg font-bold text-slate-800">Create team</h3>
            <p className="text-sm text-slate-500">Add a new team and invite members</p>
          </div>
          <button
            type="button"
            onClick={() => onClose?.()}
            className="p-2 rounded-lg hover:bg-slate-100 transition"
            aria-label="Close create team"
          >
            <X className="w-5 h-5 text-slate-600" />
          </button>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
          <label className="flex flex-col">
            <span className="text-xs text-slate-600 mb-1">Team name</span>
            <input
              value={form.name}
              onChange={(e) => setField("name", e.target.value)}
              className="px-3 py-2 bg-white border border-slate-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500/20"
              placeholder="e.g. Product Development"
            />
            {errors.name && <span className="text-rose-600 text-xs mt-1">{errors.name}</span>}
          </label>

          <label className="flex flex-col">
            <span className="text-xs text-slate-600 mb-1">Lead</span>
            <input
              value={form.lead}
              onChange={(e) => setField("lead", e.target.value)}
              className="px-3 py-2 bg-white border border-slate-200 rounded-lg focus:outline-none"
              placeholder="Team lead name"
            />
          </label>

          <label className="flex flex-col md:col-span-2">
            <span className="text-xs text-slate-600 mb-1">Description</span>
            <textarea
              value={form.description}
              onChange={(e) => setField("description", e.target.value)}
              rows={3}
              className="px-3 py-2 bg-white border border-slate-200 rounded-lg focus:outline-none resize-none"
              placeholder="Short description of team responsibilities"
            />
          </label>

          <label className="flex flex-col">
            <span className="text-xs text-slate-600 mb-1">Projects</span>
            <input
              type="number"
              value={form.projects}
              onChange={(e) => setField("projects", e.target.value)}
              className="px-3 py-2 bg-white border border-slate-200 rounded-lg focus:outline-none"
              min={0}
            />
          </label>

          <label className="flex flex-col">
            <span className="text-xs text-slate-600 mb-1">Completion (%)</span>
            <input
              type="number"
              value={form.completion}
              onChange={(e) => setField("completion", e.target.value)}
              className="px-3 py-2 bg-white border border-slate-200 rounded-lg focus:outline-none"
              min={0}
              max={100}
            />
            {errors.completion && (
              <span className="text-rose-600 text-xs mt-1">{errors.completion}</span>
            )}
          </label>
        </div>

        {/* Gradient selector */}
        <div className="mb-4">
          <span className="text-xs text-slate-600 mb-2 block">Accent</span>
          <div className="flex gap-2 flex-wrap">
            {gradients.map((g) => (
              <button
                key={g}
                type="button"
                onClick={() => setField("gradient", g)}
                className={`w-12 h-10 rounded-lg flex items-center justify-center shadow-sm border-2 ${
                  form.gradient === g ? "border-slate-300 scale-105" : "border-transparent"
                } transition-transform`}
              >
                <div className={`w-full h-full rounded-md bg-gradient-to-br ${g}`} />
              </button>
            ))}
            <label className="ml-2 flex items-center gap-2 text-sm text-slate-600">
              <input
                type="checkbox"
                checked={form.featured}
                onChange={(e) => setField("featured", e.target.checked)}
                className="accent-blue-500"
              />
              Featured
            </label>
          </div>
        </div>

        {/* Members */}
        <div className="mb-4">
          <div className="flex items-center justify-between mb-2">
            <span className="text-sm font-medium text-slate-700">Members</span>
            <button
              type="button"
              onClick={handleAddMember}
              className="inline-flex items-center gap-2 px-3 py-1.5 rounded-lg bg-slate-100 hover:bg-slate-200"
            >
              <UserPlus className="w-4 h-4" />
              Add
            </button>
          </div>
          <div className="grid gap-2">
            {form.members.map((m: string, i: number) => (
              <div key={i} className="flex items-center gap-2">
                <div className="w-10 h-10 rounded-full bg-slate-100 flex items-center justify-center text-xs font-semibold text-slate-600">
                  {memberInitials(m)}
                </div>
                <input
                  value={m}
                  onChange={(e) => handleMemberChange(i, e.target.value)}
                  placeholder="Full name (e.g. Jane Doe)"
                  className="flex-1 px-3 py-2 bg-white border border-slate-200 rounded-lg"
                />
                <button
                  type="button"
                  onClick={() => handleRemoveMember(i)}
                  className="p-2 rounded-lg hover:bg-slate-100"
                  aria-label={`Remove member ${i + 1}`}
                >
                  <X className="w-4 h-4 text-slate-500" />
                </button>
              </div>
            ))}
          </div>
        </div>

        <div className="flex items-center justify-end gap-3">
          <button
            type="button"
            onClick={() => onClose?.()}
            className="px-4 py-2 rounded-xl bg-slate-100 hover:bg-slate-200"
          >
            Cancel
          </button>
          <button
            type="submit"
            className="px-4 py-2 rounded-xl bg-gradient-to-br from-blue-500 to-cyan-500 text-white font-medium"
          >
            <span className="inline-flex items-center gap-2">
              <Plus className="w-4 h-4" />
              Create team
            </span>
          </button>
        </div>
      </motion.form>
    </div>
  );
}
